<script>

export default {
  props: ['shell_form', 'type_select', 'fullHeight', 'fullWidth' ],
  name: 'ShellDetail',
  data() {
    return {}
  },
  methods: {
    copy(e, data) {
      // console.log(e)
      // console.log(e.path[1].innerText.split("\n")[1])

      data = e.path[1].innerText.replaceAll("复制\n", "")
      let input = document.createElement('input')
      input.value = data
      input.id = 'creatDom'
      document.body.appendChild(input)
      input.select()
      document.execCommand('copy')
      this.$message.success("复制成功")
      document.body.removeChild(input)

    }

  },
  mounted() {
  },

  created() {
    // console.log('shellDetail..')
  },

}
</script>

