<template>
  <div>
    <h3 style="float: left; margin-left: 30px">Shell辅助</h3>
    <a-divider/>
    <div style="float: left; margin-left: 80px; text-align: left">
      <a-tag style="margin-left: -50px" color="red">配置信息</a-tag>

      <div>
        IP:
        <a-input style="width: 200px; margin-left: 20px"
                 v-model="shell_form.address"
                 placeholder="192.168.1.1"
        />
      </div>
      <br/>
      <div style="margin-top: -10px">
        端口 :
        <a-input style="width: 200px" v-model="shell_form.port" placeholder="666"/>
      </div>
    </div>

    <div style="float:right; text-align: left; margin-right: 50px; margin-top: 0px;">
      <a-tag style="margin-left: -50px" color="red">监听端</a-tag>
      <br>
      <span>MAC: nc -l 334</span>
      <br>
      <span>LINUX: nc -lvvp 334</span>
      <br>
      <span>WINDOWS: nc.exe -l -p334</span>
    </div>
    <!--        <p>{{ this.shell_form.address }}</p>-->
    <!--        <p>{{ this.shell_form.port }}</p>-->

    <div style="margin-top: 130px; overflow: auto">
      <a-tag style="float: left; margin-left: 30px; " color="red">目标端</a-tag>
<!--      <br/>-->
      <a-divider/>
      <div style="float: left;user-select: none; margin-top: -23px">

        <a-menu @click="handleClick" class="shellMenu"
                :style="'background-color: #f3f5fd; width: 120px; height: '+ (fullHeight-200)+'px;'"
                :selectedKeys="[$route.path]" style="overflow: auto" :defaultSelectedKeys="defaultselectedkeys">
          <a-menu-item v-for="(value, key) in this.title_data" style="border-radius: 4px; height: 35px;"
                       :key="'/shell/Detail/'+key" :class="[{'ant-menu-item-selected': title_data[key]}]">
            {{ key }}
          </a-menu-item>
        </a-menu>
      </div>

      <div style="float: right; margin-top: 10px">
        <shell-detail v-bind:shell_form="this.shell_form" v-bind:type_select="type_select" v-bind:fullHeight="fullHeight" v-bind:fullWidth="fullWidth"></shell-detail>
        <!--            <router-view></router-view>-->
      </div>
    </div>

  </div>
</template>



<script>

import ShellDetail from './ShellDetail'

export default {
  name: 'Shellsupport',
  components: { ShellDetail},
  data() {

    return {
      defaultselectedkeys: ['/shell/Detail/TCP'],
      shell_form: {
        address: '',
        port: '',
      },
      shell_rules: {},

      shell_input: '',
      // 菜单栏按钮的点击选中状态

      discard_button: '',

      title_data: {
        'TCP': false,
        'UDP': false,
        'ZSH': false,
        'Netcat': false,
        'Ncat': false,
        'Telnet': false,
        'Socat': false,
        'Perl': false,
        'Python2': false,
        'Python3': false,
        'PHP': false,
        'Ruby': false,
        'OpenSSL': false,
        'Powershell': false,
        'Awk': false,
        'TCLsh': false,
        'Java': false,
        'War': false,
        'lua': false,
        'NodeJS': false,
        'Groovy': false,
        'MeterPreter': false,
        'Xterm': false,
        'Redis': false,
      },
      // 共享的类型选择数据
      type_select: '',
      fullHeight: document.documentElement.clientHeight,
      fullWidth: document.documentElement.clientWidth,
    }
  },
  filters: {
    ellipsis(value) {
      if (!value) return ''
      if (value === 'Elevation of Privilege') {
        return '提权'
      }
      return 'RCE'
    },
  },

  mounted() {

  },

  methods: {
    change(msg) {
      this.shell_form = msg
    },

    handleClick(e) {
      // 点击之后 设置标签变色
      this.title_data[e.key.split('/')[3]] = true
      // console.log(e.key.split('/')[3])
      this.type_select = e.key.split('/')[3]
      if (this.discard_button === e.key.split('/')[3]) return
      this.title_data[this.discard_button] = false
      this.discard_button = e.key.split('/')[3]
      // this.$router.push("/")
      // this.$router.push(e.key)
    },

    ShellInput(value) {
      this.shell_input = value
      // console.log('changed', value)
    },

    // 返回顶部的定位dom函数
    backtopTarget() {
      return document.getElementById('privilegeId')
    },

  },

  created() {
    this.overtimer = window.setInterval(() => {
      setTimeout(() => {
        // console.log(document.documentElement.clientHeight, "antivirus ")
        this.fullHeight = document.documentElement.clientHeight
        this.fullWidth = document.documentElement.clientWidth
      })

    }, 1000)
    this.$once('hook:beforeDestroy', () => {
      clearInterval(this.overtimer);
      this.overtimer = null;
    })

  },

}
</script>
